import React, { useState, useEffect } from 'react';
import { Button, Layout, Menu, Dropdown, Avatar, Divider } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { Link, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import noteServer from '../../services/notes';
import { routers } from '../../pages/main';
import { userLogoutAction } from '../../actions/userAction';
import Weather from '../../components/weather';
import './index.css';

const { Header, Sider, Content } = Layout;

const MainLayout = ({ children }) => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state);
  const {
    replace,
    location: { pathname },
  } = useHistory();
  const [currentKey, setCurrentKey] = useState('');

  const handleLogout = () => {
    noteServer.setToken(null);
    dispatch(userLogoutAction());
    replace('/login');
  };

  useEffect(() => {
    let _currentKey = pathname;
    if (pathname.startsWith('/main/note')) {
      _currentKey = '/main/notes';
    } else if (pathname === '/main/') {
      _currentKey = '/main/home';
    }
    setCurrentKey(_currentKey);
  }, []);

  return (
    <Layout id="components-layout-demo-custom-trigger">
      <Sider
        style={{
          overflow: 'auto',
          height: '100vh',
          position: 'fixed',
          left: 0,
        }}
      >
        <div className="logo" />
        <Menu
          theme="dark"
          mode="inline"
          selectedKeys={[currentKey]}
          onClick={(event) => setCurrentKey(event.key)}
        >
          {routers.map((item) => {
            return (
              !item.hide && (
                <Menu.Item key={item.path} icon={<UserOutlined />}>
                  <Link to={item.path}>{item.title}</Link>
                </Menu.Item>
              )
            );
          })}
        </Menu>
      </Sider>
      <Layout className="site-layout" style={{ marginLeft: 200 }}>
        <Header className="site-layout-header">
          <Weather style={{ marginRight: 20 }} />
          <Dropdown
            overlay={
              <Menu>
                <Menu.Item key="0">
                  <Button type="link">关于我</Button>
                </Menu.Item>
                <Menu.Divider />

                <Menu.Item key="1">
                  <Button type="link">
                    <span>当前登录人：</span>
                    <span>{user && user.name}</span>
                  </Button>
                </Menu.Item>
                <Menu.Divider />

                <Menu.Item key="2">
                  <Button type="link" onClick={handleLogout}>
                    退出
                  </Button>
                </Menu.Item>
              </Menu>
            }
            trigger={['click']}
          >
            <Avatar
              style={{ backgroundColor: '#87d068' }}
              icon={<UserOutlined />}
            />
          </Dropdown>
        </Header>
        <Content
          className="site-layout-content"
          style={{
            margin: '24px 16px',
            padding: 24,
            minHeight: 280,
          }}
        >
          {children}
        </Content>
      </Layout>
    </Layout>
  );
};

export default MainLayout;
